.spinner-container {
  align-items: center;
  display: flex;
  height: 100%;
  justify-content: center;
  width: 100%;
}

.editor-visual-editor > div {
  width: 100%;
}

#mailpoet-email-editor .editor-header__toolbar {
  flex-grow: 1;
}
#mailpoet-email-editor .editor-header__center {
  flex-grow: 3;
}

.mailpoet-email-sidebar__email-type-info .components-panel__row {
  align-items: flex-start;
}
.mailpoet-email-sidebar__email-type-info .mailpoet-email-type-info__icon {
  flex: 0 0 24px;
  margin-left: 0;
  margin-right: 12px;
}
.mailpoet-email-sidebar__email-type-info .mailpoet-email-type-info__content {
  flex-grow: 1;
  margin-bottom: 4px;
}
.mailpoet-email-sidebar__email-type-info .mailpoet-email-type-info__content h2 {
  font-size: 13px;
  line-height: 24px;
  margin: 0 0 4px;
}
.mailpoet-email-sidebar__email-type-info .mailpoet-email-type-info__content span {
  font-size: 13px;
}
.mailpoet-email-sidebar__email-type-info .mailpoet-email-type-info__content p:last-child {
  margin-bottom: 0;
}
.mailpoet-email-sidebar__email-type-info .mailpoet-email-type-info__content_heading {
  display: flex;
}
.mailpoet-email-sidebar__email-type-info .mailpoet-email-type-info__content_heading div:last-child {
  margin-left: auto;
  margin-right: -8px;
  margin-top: -6px;
}

.mailpoet-settings-panel__preheader-text {
  margin-top: 9px;
}

.mailpoet-email-editor__settings-panel .mailpoet-settings-panel__richtext {
  border: 1px solid var(--wp-components-color-gray-600, #949494);
  border-radius: 2px;
  box-shadow: transparent 0 0 0;
  display: block;
  min-height: 80px;
  padding: 9px 11px;
  width: 100%;
}
.mailpoet-email-editor__settings-panel .mailpoet-settings-panel__personalization-tags-button.components-button.has-icon {
  display: inline-block;
  height: 14px;
  line-height: 14px;
  padding: 0 5px;
}
.mailpoet-email-editor__settings-panel .mailpoet-settings-panel__personalization-tags-button.components-button.has-icon span {
  height: 11px;
}
.mailpoet-email-editor__settings-panel .mailpoet-settings-panel__subject .components-base-control__label {
  width: 100%;
}
.mailpoet-email-editor__settings-panel .mailpoet-settings-panel__subject .components-base-control__label .components-external-link {
  float: right;
}
.mailpoet-email-editor__settings-panel .mailpoet-settings-panel__help {
  margin-bottom: 20px;
}
.mailpoet-email-editor__settings-panel .mailpoet-settings-panel__help .components-text {
  color: #757575;
}
.mailpoet-email-editor__settings-panel .mailpoet-settings-panel__preview-text .components-base-control__label {
  width: 100%;
}
.mailpoet-email-editor__settings-panel .mailpoet-settings-panel__preview-text-length {
  color: #000;
  display: inline-block;
  float: right;
  padding: 3px;
}
.mailpoet-email-editor__settings-panel .mailpoet-settings-panel__preview-text-length-warning {
  color: #f0b849;
}
.mailpoet-email-editor__settings-panel .mailpoet-settings-panel__preview-text-length-error {
  color: #cc1818;
}

.edit-post-visual-editor {
  line-height: 1.4; /* Recommended line-height that is also used in the email editor */
  margin: 0;
  min-height: 100%;
  padding: 0;
  -webkit-text-size-adjust: 100%; /* From MJMJ - Automatic test adjustment on mobile max to 100% */
  -ms-text-size-adjust: 100%; /* From MJMJ - Automatic test adjustment on mobile max to 100% */
  word-spacing: normal;
}

.visual-editor__email_layout_wrapper {
  height: 100%;
  margin: 0 auto;
  padding: 0;
  width: 100%;
}

.visual-editor__email_content_wrapper {
  display: flex;
  height: 100%;
  width: 100%;
}
.visual-editor__email_content_wrapper > div {
  width: 100%;
}
.visual-editor__email_content_wrapper.is-mobile-preview > div {
  display: block !important;
}
.visual-editor__email_content_wrapper.is-mobile-preview .editor-styles-wrapper {
  height: auto !important;
}

.block-editor-block-inspector__advanced {
  display: none;
}

[data-rich-text-comment],
[data-rich-text-format-boundary] {
  border-radius: 2px;
}

[data-rich-text-comment] {
  background-color: var(--wp-components-color-accent, var(--wp-admin-theme-color, #3858e9));
}
[data-rich-text-comment] span {
  color: var(--wp-components-color-accent-inverted, #fff);
  filter: none;
  padding: 0 2px;
}

.mailpoet-email-editor-campaign-name {
  align-items: center;
  display: flex;
  flex-direction: row;
  justify-content: center;
  min-width: 0;
}
.mailpoet-email-editor-campaign-name .components-dropdown {
  display: inline-flex;
}
.mailpoet-email-editor-campaign-name .components-button {
  min-width: 0;
  padding: 0;
}
.mailpoet-email-editor-campaign-name .mailpoet-email-campaign-name__link {
  display: inline-flex;
  height: fit-content;
  margin-right: 10px;
  margin-top: 10px;
}

.mailpoet-email-editor-campaign-name__dropdown .components-popover__content {
  min-width: 280px;
  padding: 0;
}
.mailpoet-email-editor-campaign-name__dropdown .mailpoet-email-editor-email-title-edit {
  padding: 16px;
}

.mailpoet-email-editor-save-button__dropdown .components-popover__content {
  min-width: 280px;
}

.editor-document-bar__shortcut {
  display: none !important;
}

.entities-saved-states__text-prompt p {
  display: none;
}

.mailpoet-email-editor-save-button__dropdown .components-panel__row .components-checkbox-control {
  width: 100%;
}

.components-notice.mailpoet-email-editor-validation-errors {
  margin: 0;
}
.components-notice.mailpoet-email-editor-validation-errors ul {
  list-style: disc outside;
  margin: 0 0 0 20px;
}
.components-notice.mailpoet-email-editor-validation-errors li {
  margin: 8px 0;
  padding: 0;
}
.components-notice.mailpoet-email-editor-validation-errors li:last-child {
  margin-bottom: 0;
}
.components-notice.mailpoet-email-editor-validation-errors button.components-button.is-link {
  color: #1e1e1e;
  margin: 0 4px;
}

.components-modal__frame.mailpoet-send-preview-email {
  max-width: 456px;
}

.mailpoet-send-preview-modal-footer {
  display: flex;
  gap: 12px;
  justify-content: flex-end;
  padding-top: 16px;
}

.mailpoet-send-preview-modal-notice-error {
  background-color: #fcf0f1;
  gap: 12px;
  padding: 12px 16px;
}
.mailpoet-send-preview-modal-notice-error ul {
  list-style: disc;
  margin: 0;
  padding: 5px 0 0 20px;
}

.mailpoet-send-preview-modal-notice-success {
  align-items: center;
  display: flex;
  padding-top: 2px;
}

