.background-block-support-panel .mailpoet__grid-full-width,
.border-block-support-panel .mailpoet__grid-full-width {
  grid-column: 1/3;
  margin: 0;
}

.edit-post-sidebar .components-button:focus:not(:disabled) {
  box-shadow: none;
  outline: none;
}

.mailpoet-email-editor__styles-panel {
  display: flex;
  flex-direction: column;
  height: 100%;
}
.mailpoet-email-editor__styles-panel .components-panel,
.mailpoet-email-editor__styles-panel .components-navigator-provider,
.mailpoet-email-editor__styles-panel .components-navigator-screen {
  height: 100%;
  overflow: hidden;
}
.mailpoet-email-editor__styles-panel .components-navigator-screen {
  overflow: hidden auto;
}
.mailpoet-email-editor__styles-panel .components-navigator-button {
  width: 100%;
}
.mailpoet-email-editor__styles-panel .mailpoet-email-editor__styles-header {
  margin-bottom: 0;
}
.mailpoet-email-editor__styles-panel .mailpoet-email-editor__styles-header-description {
  padding: 0 16px;
}
.mailpoet-email-editor__styles-panel h3.edit-site-global-styles-subtitle {
  margin-bottom: 0;
}
.mailpoet-email-editor__styles-panel .single-column {
  grid-column: span 1;
}
.mailpoet-email-editor__styles-panel .edit-site-global-styles-screen-typography__indicator {
  border-radius: 3px;
  height: 2em;
  line-height: 2em;
  padding: 0;
  width: 2em;
}
.mailpoet-email-editor__styles-panel .edit-site-typography-preview {
  align-items: center;
  background: #f0f0f0;
  border-radius: 2px;
