  background-color: #f0f6fc;
  margin-bottom: 1.5rem;
  padding: 0.5rem;
}
.email-editor-recent-templates-info path {
  fill: #007cba;
}

.email-editor-pattern__list-item {
  border: 1px solid #ddd;
  border-radius: 4px;
}
